  animation: mapboxgl-user-location-dot-pulse 2s infinite;
  content: "";
  position: absolute;
}
.mapboxgl-user-location-dot:after {
  border: 2px solid #fff;
  border-radius: 50%;
  box-shadow: 0 0 3px #00000059;
  box-sizing: border-box;
  content: "";
  height: 19px;
  left: -2px;
  position: absolute;
  top: -2px;
